@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400;500;600;700;800;900&display=swap);
html{min-height:100%;box-sizing:border-box}body{margin:0;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;font-family:'Montserrat', sans-serif}

.App{display:grid;min-height:100vh;grid-template-rows:1fr 1.5rem;background-color:#eff1f6}.app-content{padding:1em}

.react-tabs{-webkit-tap-highlight-color:transparent}.react-tabs__tab-list{border-bottom:1px solid #aaa;margin:0 0 10px;padding:0}.react-tabs__tab{display:inline-block;border:1px solid transparent;border-bottom:none;bottom:-1px;position:relative;list-style:none;padding:6px 12px;cursor:pointer}.react-tabs__tab--selected{background:#fff;border-color:#aaa;color:black;border-radius:5px 5px 0 0}.react-tabs__tab--disabled{color:GrayText;cursor:default}.react-tabs__tab:focus{box-shadow:0 0 5px #0188fe;border-color:#0188fe;outline:none}.react-tabs__tab:focus:after{content:"";position:absolute;height:5px;left:-4px;right:-4px;bottom:-5px;background:#fff}.react-tabs__tab-panel{display:none}.react-tabs__tab-panel--selected{display:block;overflow-y:scroll;height:600px}

.QuestionPage{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;grid-row-gap:1rem;row-gap:1rem}.question-page-section{padding:1rem;border:solid 1px black;background-color:#fff;border-color:#d1d9e6;border-color:#0d61f2}.question-title-area{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between}@media screen and (max-width: 600px){.question-title-area{-webkit-justify-content:center;justify-content:center;-webkit-flex-direction:column;flex-direction:column}}.change-question:hover{cursor:pointer}.question-area{display:grid;grid-template-columns:2fr 1fr;grid-auto-rows:600px;grid-gap:1rem;gap:1rem;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content}@media screen and (max-width: 768px){.question-area{grid-template-columns:1fr}}.question-area-video{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column}.recording-option{padding:0.5em;width:100%;text-overflow:wrap;font-weight:500;font-size:1em}.recording-option:hover{cursor:pointer}.question-area-notes{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column}.question-area-notes-input{resize:none;padding:1rem;height:80%;border-color:#0d61f2;color:#0d61f2;font-weight:bold;font-size:larger}.question-area-selection-panel{display:-webkit-flex;display:flex;-webkit-flex-wrap:wrap;flex-wrap:wrap;padding:1rem;-webkit-justify-content:center;justify-content:center}.question-selection-button{background-color:white;border:solid 1px black;-webkit-flex-basis:25%;flex-basis:25%;min-height:100px}@media screen and (max-width: 768px){.question-selection-button{-webkit-flex-basis:50%;flex-basis:50%}}@media screen and (max-width: 480px){.question-selection-button{-webkit-flex-basis:100%;flex-basis:100%}}.question-selection-button:hover{cursor:pointer;background-color:#a1ffa1;transition:ease-in-out 0.3s}

.RMM__close-button{position:absolute;top:20px;right:20px;cursor:pointer;height:36px;width:36px;background:rgba(0,0,0,0.4);border-radius:50%;border:none;text-indent:100%;overflow:hidden;color:transparent;white-space:nowrap;transition:opacity 0.365s, -webkit-transform 0.365s;transition:transform 0.365s, opacity 0.365s;transition:transform 0.365s, opacity 0.365s, -webkit-transform 0.365s;z-index:10002;-webkit-transform:scale(0);transform:scale(0);opacity:0}.RMM__close-button::before,.RMM__close-button::after{content:"";position:absolute;top:50%;left:50%;height:2px;width:24px;background:#fff}.RMM__close-button::before{-webkit-transform:translate(-50%) rotate(45deg);transform:translate(-50%) rotate(45deg)}.RMM__close-button::after{-webkit-transform:translate(-50%) rotate(-45deg);transform:translate(-50%) rotate(-45deg)}.RMM__close-button:hover{background:rgba(0,0,0,0.6)}.RMM__close-button--is-active{-webkit-transform:scale(1);transform:scale(1);opacity:1}.RMM__container{position:fixed;top:0;left:0;height:100%;width:100%;pointer-events:none;opacity:0;visibility:hidden;z-index:10000}.RMM__container--is-active{pointer-events:initial;opacity:1;visibility:visible}.RMM__body{position:relative;height:100%;padding:1rem;z-index:10001;opacity:0;transition:opacity 0.2s}.RMM__body--is-active{opacity:1;overflow-y:scroll}.RMM__body--no-padding{padding:0}.RMM__placeholder{position:fixed;top:0;left:0;transition-property:opacity, -webkit-transform;transition-property:transform, opacity;transition-property:transform, opacity, -webkit-transform;transition-duration:0.365s}@media only screen and (max-width: 480px){.RMM__body{padding:6rem 3%}}

.footer{background-color:#0d61f2;text-align:center;margin:0}.footer a{text-decoration:none;color:white}

